@media all {
    /* Shortcode [divider] (Theme own shortcode)
    --------------------------------------------------------------------------------- */
    .divider {
        border-bottom-width: 1px;
        border-color: rgb(241 245 249 / 100%);
        border-top-width: 1px;
        clear: both;
        display: block;
        margin: 1rem auto;
    }

    /* CodeColorer
    --------------------------------------------------------------------------------- */
    .codecolorer-container {
        border: none !important;
        border-left: 1px solid var(--color-grey-light) !important;
        border-right: 1px solid var(--color-grey-light) !important;
        padding-bottom: 0.5rem;
        padding-top: 0.5rem;
    }

    .codecolorer-container table td.line-numbers {
        background-color: transparent !important;
        border-right: none !important;
    }

    .codecolorer-container,
    .codecolorer {
        background-color: transparent !important;
    }

    div.codecolorer {
        padding: 5px 1.5rem !important;
    }

    /* Syntax Highlighter Evolved
    --------------------------------------------------------------------------------- */
    .wp-block-syntaxhighlighter-code > div {
        border: none !important;
        border-left: 1px solid var(--color-grey-light) !important;
        border-right: 1px solid var(--color-grey-light) !important;
        padding-bottom: 0.5rem;
        padding-top: 0.5rem;
    }

    .wp-block-syntaxhighlighter-code .syntaxhighlighter {
        margin: 0 !important;
    }

    .wp-block-syntaxhighlighter-code .syntaxhighlighter .gutter {
        color: var(--color-grey-dark) !important;
    }

    .wp-block-syntaxhighlighter-code .syntaxhighlighter .gutter .line {
        border-right: none !important;
        padding: 0 0.5em !important;
    }

    /* Syntax Highlight and code elements
    --------------------------------------------------------------------------------- */
    code,
    .syntaxhighlight,
    .codecolorer,
    .codecolorer *,
    .codecolorer-container,
    .codecolorer-container *,
    .wp-block-syntaxhighlighter-code,
    .wp-block-syntaxhighlighter-code .syntaxhighlighter a,
    .wp-block-syntaxhighlighter-code .syntaxhighlighter div,
    .wp-block-syntaxhighlighter-code .syntaxhighlighter code,
    .wp-block-syntaxhighlighter-code .syntaxhighlighter table,
    .wp-block-syntaxhighlighter-code .syntaxhighlighter table td,
    .wp-block-syntaxhighlighter-code .syntaxhighlighter table tr,
    .wp-block-syntaxhighlighter-code .syntaxhighlighter table tbody,
    .wp-block-syntaxhighlighter-code .syntaxhighlighter table thead,
    .wp-block-syntaxhighlighter-code .syntaxhighlighter table caption,
    .wp-block-syntaxhighlighter-code .syntaxhighlighter textarea {
        font: 0.9rem/1.3rem var(--font-family-code) !important;
    }

    @supports (font-variation-settings: normal) {
        code,
        .syntaxhighlight,
        .codecolorer,
        .codecolorer *,
        .codecolorer-container,
        .codecolorer-container *,
        .wp-block-syntaxhighlighter-code,
        .wp-block-syntaxhighlighter-code .syntaxhighlighter a,
        .wp-block-syntaxhighlighter-code .syntaxhighlighter div,
        .wp-block-syntaxhighlighter-code .syntaxhighlighter code,
        .wp-block-syntaxhighlighter-code .syntaxhighlighter table,
        .wp-block-syntaxhighlighter-code .syntaxhighlighter table td,
        .wp-block-syntaxhighlighter-code .syntaxhighlighter table tr,
        .wp-block-syntaxhighlighter-code .syntaxhighlighter table tbody,
        .wp-block-syntaxhighlighter-code .syntaxhighlighter table thead,
        .wp-block-syntaxhighlighter-code .syntaxhighlighter table caption,
        .wp-block-syntaxhighlighter-code .syntaxhighlighter textarea {
            font: 0.9rem/1.3rem var(--font-family-code-font-variation) !important;
        }
    }

    .prose .wp-block-syntaxhighlighter-code code::after,
    .prose .wp-block-syntaxhighlighter-code code::before {
        content: "";
    }

    /* Copy to Clipboard Button
    _________________________________________________________________________________ */
    .syntaxhighlighter {
        border-radius: 8px;
        border-top: none;
        position: relative;
    }

    .wp-block-syntaxhighlighter-code > .copy-to-clipboard {
        backdrop-filter: var(--backdrop-filter-default);
        background-color: var(--background-color-light-transparency);
        border: ridge 1px var(--color-grey-dark);
        border-radius: 5px;
        padding: 0.45rem 0.35rem 0.35rem;
        margin-right: 0.5rem;
        text-shadow: var(--color-grey-light)0 0 2px;
        transition: var(--background-hover-transition);
        position: sticky;
        top: 5rem;
        z-index: 1;
        width: auto;
        float: right;
    }

    .wp-block-syntaxhighlighter-code > .copy-to-clipboard:hover {
        background-color: var(--color-grey-light);
        cursor: pointer;
    }
}
